import "./App.css";

import React, { Component } from "react";
import Peer from "peerjs";
import Game from "./Game";
import MainMenu from "./MainMenu";
import { withRouter } from "react-router";

export const LOCAL_GAME = "local";
export const ONLINE_GAME = "online";

class App extends Component {
    constructor(props) {
        super(props);
        this.peer = new Peer();

        this.state = {
            peer_data: {
                err: "",
                connecting: false,
                loaded: false,
                id: "",
            },
            game_data: {
                active: false,
                type: LOCAL_GAME,
                online_data: {
                    conn: null,
                    is_host: false,
                },
            },
        };

        this.localGameStart = this.localGameStart.bind(this);
        this.onlineGameStart = this.onlineGameStart.bind(this);
        this.endGameSession = this.endGameSession.bind(this);
        this.connectToId = this.connectToId.bind(this);
    }

    componentDidMount() {
        var id_in_url = "id" in this.props.match.params
        console.log(this.props.match)

        this.peer.on("error", (error) => {
            console.log(error);
            this.setState({
                peer_data: {
                    ...this.state.peer_data,
                    err: error,
                    connecting: false,
                },
                game_data: {
                    ...this.state.game_data,
                    active: false,
                },
            });
        });

        this.peer.on("open", (id) => {
            this.setState({
                peer_data: {
                    ...this.state.peer_data,
                    loaded: true,
                    id: id,
                },
            });

            if (id_in_url) {
                var url_id = this.props.match.params["id"]
                this.connectToId(url_id)    
            }
        });

        this.peer.on("connection", (conn) => {
            conn.on("open", () => {
                this.onlineGameStart(conn, true);
            });
        });
    }

    endGameSession() {
        /*if (this.state.peer_data.connections > 0) {
            this.state.peer_data.destroy();
        }*/

        this.setState({
            game_data: { ...this.state.game_data, active: false },
        });
    }

    connectToId(id) {
        if (id === "") return

        this.setState({
            peer_data: { ...this.state.peer_data, connecting: true },
        });
        var conn = this.peer.connect(id);

        conn.on("open", () => {
            this.setState({
                peer_data: { ...this.state.peer_data, connecting: false },
            });
            this.onlineGameStart(conn, false);
        });
    }

    localGameStart() {
        this.setState({
            game_data: {
                ...this.state.game_data,
                type: LOCAL_GAME,
                active: true,
            },
        });
    }

    onlineGameStart(conn, is_host) {
        this.conn = conn;

        this.setState({
            game_data: {
                ...this.state.game_data,
                type: ONLINE_GAME,
                active: true,
                online_data: {
                    ...this.state.game_data.online_data,
                    is_host: is_host,
                },
            },
        });
    }

    render() {
        return (
            <div className="App">
                <h1>HARES AND HOUNDS</h1>

                <header className="App-header">
                    {!this.state.game_data.active ? (
                        <MainMenu
                            peer_data={this.state.peer_data}
                            localGameStart={this.localGameStart}
                            attemptConnectionCallback={this.connectToId}
                        />
                    ) : (
                        <Game
                            game_data={this.state.game_data}
                            EndGameSessionCallback={this.endGameSession}
                            conn={this.conn}
                        />
                    )}
                </header>

                <p><b>Grid made by NeoK12</b></p>
            </div>
        );
    }
}

export default withRouter(App);
