import React, { useState } from "react";

function MainMenu({ peer_data, localGameStart, attemptConnectionCallback }) {
    var [id, change_id] = useState("");

    var peer_id = peer_data.err ? (
        <p>{peer_data.err.toString()}</p>
    ) : !peer_data.loaded ? (
        <p>Loading P2P...</p>
    ) : (
        <p>Peer id is: {peer_data.id}</p>
    );

    function handleChange(event) {
        change_id(event.target.value);
    }

    function handleCopy() {
        var url_to_copy
        if ("id_in_url" in peer_data) {
            url_to_copy = window.location.href.replace(peer_data["id_in_url"], peer_data.id);
        }
        else {
            url_to_copy = window.location.href + peer_data.id
        }
        navigator.clipboard.writeText(url_to_copy)
    }

    return (
        <div id="mainmenu">
            {peer_data.connecting ? <p>Connecting...</p> : peer_id}
            <button onClick={localGameStart}>Local Game</button>
            

            {peer_data.loaded && !peer_data.err && (
                <>
                    <input onChange={handleChange}></input>
                    <button onClick={() => attemptConnectionCallback(id)}>
                        Connect
                    </button>
                    <button onClick={handleCopy}>Copy link</button>
                </>
            )}
        </div>
    );
}

export default MainMenu;
