import React, { Component } from "react";
import { ONLINE_GAME } from "./App";

const HOUND = "hound";
const HARE = "hare";
const EMPTY = "empty";

const UPDATE_GAME = "update_game";
const RESET_GAME = "reset_game";

class Game extends Component {
    constructor(props) {
        super(props);

        this.game_data = this.props.game_data;
        this.game_type = this.game_data.type;

        this.hare_location = "";
        this.hound_location = new Set();

        this.conn = this.props.conn;

        this.default_selected_node = {
            y_pos: 0,
            x_pos: 0,
            type: EMPTY,
        };

        this.default_matrix = {
            1: {
                2: {},
                3: {},
                4: {},
            },
            2: {
                1: {},
                2: {},
                3: {},
                4: {},
                5: {},
            },
            3: {
                2: {},
                3: {},
                4: {},
            },
        };

        /*
            node information:
            {
                type: (empty|hare|hound)
                paths: [
                    [everyone], [haresonly]
                ]
            }
        */
        this.default_state = {
            matrix: this.default_matrix,
            selected_node: this.default_selected_node,
            current_turn: HOUND,
            turn_number: 1,
            game_in_progress: false,
            winning_side: "",
        };

        this.state = {
            ...this.default_state,
            player_side: "",
        };

        this.default_state = this.state;
        this.setGameMatrix();

        this.selectNode = this.selectNode.bind(this);

        this.resetGame = this.resetGame.bind(this);
        this.endGame = this.endGame.bind(this);
        this.handleReset = this.handleReset.bind(this);
    }

    componentDidMount() {
        if (this.game_type === ONLINE_GAME) {
            if (this.conn === null) {
                console.log("no connection");
                this.EndGameSessionCallback();
                return;
            }

            this.setState({
                player_side: this.game_data.online_data.is_host ? HOUND : HARE,
            });

            this.conn.on("data", (data) => {
                switch (data.type) {
                    case UPDATE_GAME:
                        var [old_y, old_x] = data.old_position.split(",");
                        var [new_y, new_x] = data.new_position.split(",");
                        this.updateNodePosition(old_y, old_x, new_y, new_x);
                        this.setState({ selected_node: this.default_selected_node });
                        this.updateGameRound();
                        break;
                    case RESET_GAME:
                        this.resetGame();
                        break;
                    default:
                        break;
                }
            });

            this.conn.on("error", (err) => {
                console.log(err);
                this.props.EndGameSessionCallback();
            });

            this.conn.on("close", () => {
                this.props.EndGameSessionCallback();
            });
        }

        this.setState({ game_in_progress: true });
    }
    
    setGameMatrix() {
        var matrix = this.state.matrix;

        // populating position with default key-values & adding routes
        for (var y_pos in matrix) {
            for (var x_pos in matrix[y_pos]) {
                y_pos = parseInt(y_pos);
                x_pos = parseInt(x_pos);

                matrix[y_pos][x_pos]["type"] = EMPTY;
                matrix[y_pos][x_pos]["paths"] = [new Set(), new Set()];

                for (var y_shift = -1; y_shift <= 1; y_shift++) {
                    for (var x_shift = -1; x_shift <= 1; x_shift++) {
                        var shifted_y_pos = y_pos + y_shift;
                        var shifted_x_pos = x_pos + x_shift;

                        if (
                            (x_pos % 2 === 0 && y_pos % 2 === 0) ||
                            (y_pos % 2 !== 0 && x_pos % 2 !== 0)
                        ) {
                            var fourdirection = new Set([
                                "-1,0",
                                "1,0",
                                "0,-1",
                                "0,1",
                            ]);
                            if (!fourdirection.has(`${y_shift},${x_shift}`)) {
                                continue;
                            }
                        }

                        if (
                            shifted_y_pos in matrix &&
                            shifted_x_pos in matrix[shifted_y_pos] &&
                            !(
                                shifted_x_pos === x_pos &&
                                shifted_y_pos === y_pos
                            )
                        ) {
                            var position_str = `${shifted_y_pos},${shifted_x_pos}`;

                            // hare-only path
                            if (shifted_x_pos < x_pos) {
                                matrix[y_pos][x_pos]["paths"][1].add(
                                    position_str
                                );
                            }

                            // hound & hare path
                            else {
                                matrix[y_pos][x_pos]["paths"][0].add(
                                    position_str
                                );
                            }
                        }
                    }
                }
            }
        }

        // setting piece positions
        matrix[1][2]["type"] = HOUND;
        matrix[2][1]["type"] = HOUND;
        matrix[3][2]["type"] = HOUND;

        ["1,2", "2,1", "3,2"].forEach((a) => this.hound_location.add(a));

        matrix[2][5]["type"] = HARE;
        this.hare_location = "2,5";

        this.setState({ matrix: matrix });
    }

    updateNodePosition(old_y_pos, old_x_pos, new_y_pos, new_x_pos) {
        var matrix = this.state.matrix;
        var movingNodeType = matrix[old_y_pos][old_x_pos]["type"];

        matrix[old_y_pos][old_x_pos]["type"] = EMPTY;
        matrix[new_y_pos][new_x_pos]["type"] = movingNodeType;

        if (movingNodeType === HOUND) {
            this.hound_location.delete(`${old_y_pos},${old_x_pos}`);
            this.hound_location.add(`${new_y_pos},${new_x_pos}`);
        } else {
            this.hare_location = `${new_y_pos},${new_x_pos}`;
        }

        this.setState({
            matrix: matrix,
        });
    }

    selectNode(y_pos, x_pos) {
        if (
            (this.game_type === ONLINE_GAME &&
                this.state.current_turn !== this.state.player_side) ||
            !this.state.game_in_progress
        ) {
            return;
        }

        var clicked_node = this.state.matrix[y_pos][x_pos];
        
        if (
            this.state.selected_node["type"] !== EMPTY &&
            clicked_node["type"] === EMPTY
        ) {
            var valid_play = false;

            if (this.state.selected_node["type"] === HOUND) {
                if (
                    this.state.selected_node["paths"][0].has(
                        `${y_pos},${x_pos}`
                    )
                ) {
                    this.updateNodePosition(
                        this.state.selected_node.y_pos,
                        this.state.selected_node.x_pos,
                        y_pos,
                        x_pos
                    );

                    valid_play = true;
                }
            } else {
                if (
                    this.state.selected_node["paths"][0].has(
                        `${y_pos},${x_pos}`
                    ) ||
                    this.state.selected_node["paths"][1].has(
                        `${y_pos},${x_pos}`
                    )
                ) {
                    this.updateNodePosition(
                        this.state.selected_node.y_pos,
                        this.state.selected_node.x_pos,
                        y_pos,
                        x_pos
                    );

                    valid_play = true;
                }
            }

            if (valid_play) {
                if (this.game_type === ONLINE_GAME) {
                    this.conn.send({
                        type: UPDATE_GAME,
                        old_position: `${this.state.selected_node.y_pos},${this.state.selected_node.x_pos}`,
                        new_position: `${y_pos},${x_pos}`,
                    });
                }

                this.setState({ selected_node: this.default_selected_node });
                this.updateGameRound();
            }
        }

        // havent made a selection yet or choosing another
        else {
            if (clicked_node["type"] === this.state.current_turn) {
                this.setState({
                    selected_node: {
                        y_pos: y_pos,
                        x_pos: x_pos,
                        type: clicked_node["type"],
                        paths: clicked_node["paths"],
                    },
                });
            } else {
                this.setState({
                    selected_node: this.default_selected_node,
                });
            }
        }
    }

    updateGameRound() {
        console.log("Before set:", this.state.turn_number);
        this.setState({ turn_number: this.state.turn_number + 1 }, () => 
        {
            console.log("After set:", this.state.turn_number);

            // WIN CONDITION FOR HARE
            var hare_past_hounds = true;
    
            this.hound_location.forEach((hound) => {
                var hound_x = hound.toString().split(",")[1];
                var hare_x = this.hare_location.split(",")[1];
    
                if (hare_x > hound_x) {
                    hare_past_hounds = false;
                }
            });
    
            console.log(this.state.turn_number);
            if (this.state.turn_number > 20 || hare_past_hounds) {
                console.log("hare wins!");
                this.setState({ game_in_progress: false, winning_side: HARE });
            }
    
            // WIN CONDITION FOR HOUND
            var hare_trapped = true;
            var [hare_y, hare_x] = this.hare_location.split(",");
            var paths = this.state.matrix[hare_y][hare_x]["paths"];
            for (var setOfPath in paths) {
                paths[setOfPath].forEach(
                    // eslint-disable-next-line no-loop-func
                    (path) => {
                        var [path_y, path_x] = path.toString().split(",");
    
                        if (this.state.matrix[path_y][path_x]["type"] === EMPTY) {
                            hare_trapped = false;
                        }
                    }
                );
            }
    
            if (hare_trapped) {
                console.log("hounds wins!");
                this.setState({ game_in_progress: false, winning_side: HOUND });
            }
    
            var turn = this.state.current_turn === HARE ? HOUND : HARE;
            this.setState({ current_turn: turn });
        });

    }

    endGame() {
        if (this.game_type === ONLINE_GAME) {
            this.conn.close();
        } else {
            this.props.EndGameSessionCallback();
        }
    }

    handleReset() {
        this.resetGame();
        if (this.game_type === ONLINE_GAME) {
            this.conn.send({ type: RESET_GAME });
        }
    }

    resetGame() {
        this.hare_location = "";
        this.hound_location = new Set();

        this.setState({ ...this.state, ...this.default_state });
        this.setGameMatrix();

        if (this.game_type === ONLINE_GAME) {
            console.log(this.state.player_side);
            // swap sides
            this.setState({
                player_side: this.state.player_side === HOUND ? HARE : HOUND,
            });
        }

        this.setState({ game_in_progress: true });
    }

    getGameNodes() {
        var game_nodes = [];

        for (var y_pos in this.state.matrix) {
            for (var x_pos in this.state.matrix[y_pos]) {
                var currentNode = this.state.matrix[y_pos][x_pos];
                var isHighlighted = false;
                var isPlayable = false;

                if (this.state.selected_node !== this.default_selected_node) {
                    // show avaliable paths for selected node
                    if (
                        this.state.selected_node["type"] === HOUND &&
                        this.state.selected_node["paths"][0].has(
                            `${y_pos},${x_pos}`
                        ) &&
                        currentNode["type"] === EMPTY
                    ) {
                        isHighlighted = true;
                        isPlayable = true;
                    }

                    // case for hare
                    else if (
                        this.state.selected_node["type"] === HARE &&
                        (this.state.selected_node["paths"][0].has(
                            `${y_pos},${x_pos}`
                        ) ||
                            this.state.selected_node["paths"][1].has(
                                `${y_pos},${x_pos}`
                            )) &&
                        currentNode["type"] === EMPTY
                    ) {
                        isHighlighted = true;
                        isPlayable = true;
                    }
                }

                if (
                    this.game_type === ONLINE_GAME &&
                    this.state.current_turn !== this.state.player_side
                ) {
                    isPlayable = false;
                } else if (currentNode["type"] === this.state.current_turn) {
                    isPlayable = true;
                }

                if (!this.state.game_in_progress) {
                    isPlayable = false;
                    isHighlighted = false;
                }

                game_nodes.push(
                    <GameNode
                        x_pos={x_pos}
                        y_pos={y_pos}
                        type={currentNode["type"]}
                        highlighted={isHighlighted}
                        onClick={this.selectNode}
                        key={`${y_pos}-${x_pos}`}
                        playable={isPlayable}
                    ></GameNode>
                );
            }
        }

        return game_nodes;
    }

    render() {
        var game_nodes = this.getGameNodes();

        return (
            <>
                <h1>{this.game_type.toUpperCase()} GAME</h1>
                <div className="game">
                    <img
                        src="https://www.neok12.com/games/hare-hounds/grid.png"
                        alt=""
                        key="gameboard"
                    ></img>
                    {game_nodes}
                </div>
                <div className="information">
                    {this.state.winning_side !== "" ? (
                        <p>
                            <b>{this.state.winning_side.toUpperCase()}</b> wins{" "}
                        </p>
                    ) : (
                        <p>
                            Current turn: <b>{this.state.current_turn.toUpperCase()}S</b>
                        </p>
                    )}

                    {this.game_type === ONLINE_GAME && (
                        <p>You are: {this.state.player_side}</p>
                    )}
                </div>
                <div className="buttons">
                    {!this.state.game_in_progress && (
                        <button onClick={this.handleReset}>Reset Game</button>
                    )}
                    <button onClick={this.endGame}>Return Back</button>
                </div>
            </>
        );
    }
}

function GameNode({ x_pos, y_pos, type, highlighted, onClick, playable }) {
    var style = {
        left: (x_pos - 1) * 139 + 23,
        top: (y_pos - 1) * 120 + 27,
    };

    var className = `node ${type || `${y_pos},${x_pos}`} ${
        highlighted ? "highlighted" : ""
    } ${playable ? "playable" : ""}`;

    var handleClick = () => onClick(y_pos, x_pos);

    return <div className={className} style={style} onClick={handleClick} />;
}

// eslint-disable-next-line no-unused-vars
function coordinateToText(x_pos, y_pos) {
    return `${x_pos},${y_pos}`;
}

// eslint-disable-next-line no-unused-vars
function textToCoordinate(string) {
    return string.split(",");
}

export default Game;
